@-webkit-keyframes slide {
	0% {
		left: -90vw;
	}
	100% {
		left: 0;
	}
}

@-moz-keyframes slide {
	0% {
		left: -90vw;
	}
	100% {
		left: 0;
	}
}

@-o-keyframes slide {
	0% {
		left: -90vw;
	}
	100% {
		left: 0;
	}
}

@keyframes slide {
	0% {
		left: -90vw;
	}
	100% {
		left: 0;
