.single-caso .case-content .case__wrap .enlace a:hover {
	border: 0;
	opacity: .8;
}

.single-caso .case-content .comments-area {
	max-width: 60%;
}

.single-caso #photo-viewer {
	position: relative;
	height: 300px;
	overflow: hidden;
	background: white;
	border: 2px solid #41423F;
	-moz-border-radius: 2px;
	-webkit-border-radius: 2px;
	border-radius: 2px;
}

.single-caso #photo-viewer img {
	height: 100%;
	width: auto;
}

.single-caso #photo-viewer.is-loading:after {
	content: url("img/load.gif");
	position: absolute;
	top: 50%;
	left: 50%;
}

.single-caso #photo-viewer img {
	position: absolute;
	max-width: 100%;
	max-height: 100%;
	top: 50%;
	left: 50%;
}

.single-caso #thumbnails {
	margin: 10px .5em;
	padding-bottom: .25em;
	height: 80px;
	display: flex;
	align-items: center;
	border-bottom: 1px solid #41423F;
}

.single-caso #thumbnails a {
	height: 100%;
	width: auto;
	margin: 2px;
	border: 2px solid #41423F;
	-moz-border-radius: 2px;
	-webkit-border-radius: 2px;
	border-radius: 2px;
}

.single-caso #thumbnails a img {
	height: 100%;
	width: auto;
}

.single-caso a.active {
	opacity: 0.3;
}

/*--------------------------------------------------------------
## Casos pages
--------------------------------------------------------------*/
.casos-index .content-area {
	padding: 1em;
}

@media screen and (min-width: 600px) {
	.casos-index .content-area {
		padding: 1em 2em;
	}
}

.casos-index .site-main {
	max-width: 780px;
	margin: 0 auto;
}

.casos-index .case-gallery {
	width: 100%;
	display: flex;
	align-items: stretch;
	align-content: flex-start;
	flex-wrap: wrap;
}

.casos-index .case-gallery:before {
	content: "Explore los casos:";
	display: block;
	width: 100%;
	font-family: "Fredoka One";
	margin: 1em 0;
}

.casos-index .case-gallery .case-item {
	width: 100%;
	background: #F1EEE5;
	border: 3px solid #41423F;
	margin: 5px 5px 10px;
	padding: .5em .5em 1.5em;
	-moz-border-radius: 5px;
	-webkit-border-radius: 5px;
	border-radius: 5px;
	position: relative;
	box-shadow: 4px 4px 8px 0 rgba(0, 0, 0, 0.28);
}

@media screen and (min-width: 600px) {
	.casos-index .case-gallery .case-item {
		width: 18%;
		min-width: 265px;
	}
}

.casos-index .case-gallery .case-item figure {
	margin: -.5em -.5em 0;
	border: 0;
}

.casos-index .case-gallery .case-item figure a, .casos-index .case-gallery .case-item figure a:hover {
	border: none;
}

.casos-index .case-gallery .case-item h2 {
	font-size: 120%;
	margin: 0.25em 0;
}

.casos-index .case-gallery .case-item .excerpt {
	font-size: .7em;
	line-height: 1.3;
	margin: .25em 0;
}

.casos-index .case-gallery .case-item .continue-reading a {
	font-size: .8em;
	display: inline-block;
	margin-left: auto;
	background: #D96C70;
	padding: .25em 1em;
	position: absolute;
	bottom: 5px;
	right: -10px;
	color: solid #41423F;
	-webkit-clip-path: polygon(95% 0, 100% 50%, 95% 100%, 0 100%, 0 0);
	clip-path: polygon(95% 0, 100% 50%, 95% 100%, 0 100%, 0 0);
}

.casos-index .case-gallery .case-item .continue-reading a:hover {
	border-bottom: 3px solid rgba(68, 66, 65, 0.49);
}

@media screen and (min-width: 1400px) {
	.casos-index .site-content {
		width: 100%;
		max-width: 1400px;
	}
	.casos-index .case-index__wrap {
		display: flex;
		width: 100%;
		justify-content: space-between;
	}
	.casos-index .case-index__wrap .site-main {
		width: 48%;
		margin: 0;
	}
	.casos-index .case-index__wrap .case-gallery {
		width: 48%;
	}
	.casos-index .case-index__wrap .case-gallery .case-item {
		width: 48%;
	}
}

/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
.comments-area {
	width: 100%;
	margin: 3em 0;
	padding: 1.5em 0;
	border-top: 1px solid #cccccc;
}

.comments-title {
	margin-bottom: 2em;
	font-size: 1.5em;
	font-weight: 900;
}

.comment-list {
	padding: 0;
	margin: 0;
	list-style-type: none;
}

.comment-list ol {
	list-style-type: none;
}

.comment-body {
	border-bottom: 1px solid #c3c3c3;
	margin-bottom: 3em;
}

.comment-meta {
	position: relative;
	margin-left: 70px;
	font-family: "Rubik", Helvetica, Arial, sans-serif;
	font-size: 80%;
}

.comment-meta a,
.comment-form a {
	text-decoration: none;
	border: none;
}

.comment-meta a:hover, .comment-meta a:focus,
.comment-form a:hover,
.comment-form a:focus {
	text-decoration: underline;
}

.comment .avatar {
	position: absolute;
	top: -5px;
	left: -70px;
	width: 50px;
	height: 50px;
	border-radius: 50px;
}

.children {
	margin-left: 1em;
	padding-left: 0;
}

@media screen and (min-width: 900px) {
	.children {
		margin-left: 2em;
	}
}

@media screen and (min-width: 900px) {
	.no-sidebar .children {
		margin-left: 2em;
	}
	.no-sidebar .children .comment-meta {
		margin-left: 80px;
		font-size: 90%;
	}
	.no-sidebar .children .comment .avatar {
		top: -7px;
		left: -80px;
		width: 60px;
		height: 60px;
	}
	.no-sidebar .children .comment-content {
		margin-left: 80px;
	}
}

@media screen and (min-width: 1200px) {
	.no-sidebar .children {
		margin-left: 3em;
	}
	.no-sidebar .comment-meta {
		margin-left: 90px;
	}
	.no-sidebar .comment .avatar {
		top: -8px;
		left: -90px;
		width: 70px;
		height: 70px;
	}
	.no-sidebar .comment-content {
		margin-left: 90px;
	}
}

.comment-metadata {
	margin-top: .2em;
	padding-bottom: .5em;
	font-size: .8em;
	line-height: 1em;
}

.comment-metadata a {
	color: #7D7D7D;
}

.comment-metadata a:hover, .comment-metadata a:focus {
	border-color: #c3c3c3;
}

.comment-author {
	font-size: 1.4em;
	line-height: 1.3em;
}

.says {
	font-size: 80%;
}

.reply {
	margin-bottom: 1.5em;
	text-align: right;
}

.reply a {
	display: inline-block;
	padding: .5em 1.4em;
	font-family: "Rubik", Helvetica, Arial, sans-serif;
	font-size: 85%;
	color: #000;
	line-height: 1.3em;
	text-decoration: none;
	border: 1px solid #c3c3c3;
}

.reply a:hover, .reply a:focus {
	color: #fff;
	background: #D96C70;
	border-color: #D96C70;
}

.comment-content ol {
	list-style-type: decimal;
}

.comment-content a {
	word-wrap: break-word;
}

.bypostauthor .avatar {
	box-sizing: content-box;
	border: 5px solid #D96C70;
	margin-top: -5px;
	margin-left: -5px;
}

/* Comment Form */
.comment-reply-title {
	margin-bottom: 1em;
}

.comment-list .comment-reply-title {
	text-align: left;
}

.comment-form {
	font-family: "Rubik", Helvetica, Arial, sans-serif;
	line-height: 1.3em;
}

.comment-form a {
	font-weight: 500;
}

.comment-form .form-submit {
	text-align: right;
}

.comment-form .form-submit input {
	padding: .5em 1.4em;
	font-size: 85%;
	color: #000;
	line-height: 1.3em;
	text-decoration: none;
	border: 1px solid #c3c3c3;
}

.comment-form .form-submit input:hover, .comment-form .form-submit input:focus {
	color: #fff;
	background: #D96C70;
	border-color: #D96C70;
}

.comment-form label {
	width: 33%;
	display: block;
	font-style: italic;
	padding-bottom: .4em;
}

.comment-form .required {
	color: #D54E21;
}

.pingback {
	font-family: "Rubik", Helvetica, Arial, sans-serif;
}

.pingback .comment-body {
	padding: 1em 0;
}

.pingback a {
	font-style: italic;
	text-decoration: none;
}

/*--------------------------------------------------------------
# Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
	/* Theme Footer (when set to scrolling) */
	display: none;
}

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */
.infinity-end.neverending .site-footer {
	display: block;
}

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
	display: inline-block;
}

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
	margin-bottom: 2.5em;
	max-width: 100%;
}

.wp-caption img[class*="wp-image-"] {
	display: block;
	margin-left: auto;
	margin-right: auto;
	padding: 2em;
	border: 2px solid #41423F;
	-moz-border-radius: 4px;
	-webkit-border-radius: 4px;
	border-radius: 4px;
	box-shadow: 2px 3px 4px 0 rgba(0, 0, 0, 0.32);
}

.wp-caption .wp-caption-text {
	margin: 0.8075em 0;
	font-size: .7em;
	color: #666;
}

.wp-caption-text {
	text-align: center;
}

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
	margin-bottom: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	vertical-align: top;
	width: 100%;
}

.gallery-columns-2 .gallery-item {
	max-width: 50%;
}

.gallery-columns-3 .gallery-item {
	max-width: 33.33%;
}

.gallery-columns-4 .gallery-item {
	max-width: 25%;
}

.gallery-columns-5 .gallery-item {
	max-width: 20%;
}

.gallery-columns-6 .gallery-item {
	max-width: 16.66%;
}

.gallery-columns-7 .gallery-item {
	max-width: 14.28%;
}

.gallery-columns-8 .gallery-item {
	max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
	max-width: 11.11%;
}

.gallery-caption {
	display: block;
}
